import React,{useState} from 'react'
import {Layout,Header,Navigation,Drawer,Content} from 'react-mdl'
import {Link} from 'react-router-dom'
import Switch from '@material-ui/core/Switch';
import {useStateValue} from '../StateProvider'
function Layouts(props) {
    const[{toggle},disptach] = useStateValue();
    const[dm,setDm] = useState(true)

    
    const handleChange=async()=>{

       await setDm(!dm)
                
       dispatchTheAction()
              
    }

    const dispatchTheAction=()=>{
        disptach({
            type:"TOGGLE_DARKMODE",
            payload:dm
        })
    }
  
    return (<div>
        <div className="demo-big-content">
        <Layout>
            <Header  className={toggle? `header-dark`:"header-color"} title="Samul Shrestha" scroll>
            Dark Mode<Switch
        checked={toggle}
        onChange={handleChange}
        name="checkedA"
        inputProps={{ 'aria-label': 'secondary checkbox' }}
      />
               
                <Navigation>
    <Link to = "/">{toggle? 'Home':'Away'}</Link>
                 <Link to = "/portfoliowebsite/resume">Resume</Link>
                  <Link to = "/projects">Projects</Link>
                 
                  <Link to = "/contacts">Contacts</Link>   
                </Navigation>
            </Header>
            <Drawer title="Navigation" className={toggle? "drawer_dark":null}>
                <Navigation>
                    

                <Link className={toggle? "darklinks":null}to = "/">Home</Link>
                 <Link className={toggle? "darklinks":null} to = "/portfoliowebsite/resume">Resume</Link>
                  <Link className={toggle? "darklinks":null} to = "/projects">Projects</Link>
                 
                  <Link className={toggle? "darklinks":null} to = "/contacts">Contacts</Link>   
                  
                </Navigation>
            </Drawer>
            <Content>
                <div className="page-content" />
                {props.children}
            </Content>

        
        </Layout>
       
    </div>
   
   
</div>
    )
}

export default Layouts
